<template>
  <div class="ul" v-for="item in list" :key="item.id">
    <div class="li">
      <div class="value"><span class="key">兑换地址：</span>{{item.user}}</div>
    </div>
    <template v-if="item.swapType == 0">
      <div class="li">
        <div class="value"><span class="key">兑换数量 (USDT)：</span>{{Number(item.usdtAmount/10**usdtDecimls).toFixed(6)}}</div>
      </div>
      <div class="li">
        <div class="value"><span class="key">输出数量 (ABDX)：</span>{{Number(item.tokenAmount/10**tokenDecimls).toFixed(6)}}</div>
      </div>
    </template>
    <template v-else>
      <div class="li">
        <div class="value"><span class="key">兑换数量 (ABDX)：</span>{{Number(item.tokenAmount/10**tokenDecimls).toFixed(6)}}</div>
      </div>
      <div class="li">
        <div class="value"><span class="key">输出数量 (USDT)：</span>{{Number(item.usdtAmount/10**usdtDecimls).toFixed(6)}}</div>
      </div>
    </template>
    <div class="li">
      <div class="value"><span class="key">兑换时间：</span>{{$dateformat(item.time*1000, 'yyyy-mm-dd HH:MM:ss')}}</div>
    </div>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import {useStore} from "vuex";
export default {
props: {
  list: {
    type: Array,
    default: () => {
      return []
    }
  }
},
setup(props) {
    const store = useStore()
    const { t } = useI18n()
    console.log(props)
    const data = reactive({
      $t: t,
      web3: computed(() => store.state.provider),
      address: computed(() => store.state.accounts[0]),
      abdxContract: null,
      usdtContract: null,
      tokenDecimls: 10 ** 18,
      usdtDecimls: 10 ** 18,
      list: [],
      initContract: async () => {
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.usdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.tokenDecimls = await data.abdxContract.methods.decimals().call()
        data.usdtDecimls = await data.usdtContract.methods.decimals().call()
        data.list = props.list
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.ul {
  margin-bottom: 12px;
  border-radius: 8px;
  background: #fff;
  padding: 16px 13px;
  &:last-child {
    margin-bottom: 0;
  }
  .li {
    display: flex;
    line-height: 1.3;
    padding-bottom: 10.5px;
    div {
      display: inline;
    }
    &:last-child {
      padding-bottom: 0;
    }

    .key {
      font-size: 14px;
      color: #666;
      flex-shrink: 0;
    }
    .value {
      font-size: 14px;
      color: #111;
      word-break: break-all;
    }
  }
}
</style>
