<template>
  <div class="page-box">
    <HeadTop :title="'兑换记录'"></HeadTop>
    <div class="swap">
       <van-sticky :offset-top="46" style="margin-bottom:12px;">
      <van-dropdown-menu :overlay="false">
        <van-dropdown-item v-model="value" :options="option" @change="changeHandle" />
      </van-dropdown-menu>
      </van-sticky>
      <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
        <van-list v-model:loading="loading" :immediate-check="false" v-model:error="error" error-text="请求失败，点击重新加载" :finished="finished" finished-text="没有更多了" @load="onLoad">
          <div class="list-wrap">
            <List :list="list"></List>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import List from './List'
import { reactive, toRefs, computed, watch } from "vue";
import { useRouter, useRoute, onBeforeRouteUpdate } from 'vue-router'
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getSwapLog } from "@/api";
export default {
  components: {
    HeadTop,
    List
  },
  setup () {
    const store = useStore()
    const router = useRouter()
    const route = useRoute()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      tokenDecimls: 0,
      list: [],
      option: [
        { text: '全部', value: '' },
        { text: 'USDT兑换ABDX', value: '0' },
        { text: 'ABDX兑换USDT', value: '1' },
      ],
      value: route.query.value || '',
      isOpen: false,
      page: 1,
      limit: 5,
      loading: false,
      error: false,
      finished: false,
      refreshing: false,
      initContract: async () => {
        data.initData()
      },
      initData: () => {
        data.page = 1
        data.getSwapLog()
      },
      getSwapLog () {
        getSwapLog({ address: data.address, page: data.page, limit: data.limit, type: data.value }).then((result) => {
          if (data.refreshing) {
            data.list = [];
            data.refreshing = false;
          }
          if (data.page === 1) {
            data.list = result.data.list
          } else {
            data.list.push(...result.data.list)
          }
          data.loading = false
          data.finished = !(Math.ceil(result.data.total / data.limit) > data.page)
        })
          .catch(() => {
            data.error = true
          })
      },
      onLoad () {
        console.log('onLoad')
        data.page++
        data.getSwapLog()
      },
      onRefresh () {
        data.finished = false;
        // 重新加载数据
        // 将 loading 设置为 true，表示处于加载状态
        data.loading = true;
        data.page = 1
        data.getSwapLog()
      },
      changeHandle (inject) {
        router.replace({ name: 'RecordSwap', query: { value: inject } })
      }
    })
    if (data.address) {
      data.initContract()
    }
    onBeforeRouteUpdate((to, from) => {
      console.log(from)
      data.cid = to.query.cid || '0';
      data.page = 1
      data.list = []
      data.loading = false
      data.error = false
      data.finished = false
      data.refreshing = false
      data.getSwapLog()
    });
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.swap {
  padding: 12px 15px;
  min-height: calc(100vh - 46px);
  display: flex;
  flex-direction: column;
  .van-dropdown-menu {
    flex-shrink: 0;
  }
  .van-pull-refresh {
    flex-grow: 1;
  }
  .van-dropdown-menu {
    border: 1px solid #2058e9;
    border-radius: 8px;
    overflow: hidden;
  }
  .list-wrap {
    padding-top: 12px;
  }
}
</style>
<style lang="less">
#app {
  .swap {
    .van-dropdown-item__content {
      left: 15px;
      right: 15px;
      width: initial;
      top: 1px;
      border-radius: 8px;
      box-shadow: 1px 0 10px #d9dadb;
      overflow: hidden;
    }
  }
}
</style>
